___CSS_LOADER_EXPORT___.push([module.id, `:root{
    --text: #080302;
    --background: #fefbfb;
    --primary: #9ed9e6;
    --secondary: #ebbcb2;
    --accent: #2b90a6;
    --white: white;
    --shadow: black;
}

body{
    padding:0;
    margin: 0;
    background-color: var(--background);
    display: flex;
    justify-content: center; 
    align-items: center;
    height: 100vh;
    height: 100dvh;
    font-family: 'REM', sans-serif;
}

.container{
    border-radius: 1rem;
    position: absolute;
    width: 250px;
    aspect-ratio: 1/1;
    background-color: var(--primary);
    box-shadow: 4px 4px var(--shadow);
    border: 2px solid var(--shadow);
}

a{
    padding: 0.5rem;
    background-color: var(--secondary);
    color: var(--background);
    text-decoration: none;
    box-shadow: 4px 4px var(--shadow);
    border: 2px solid var(--shadow);
    border-radius: 0.5rem;
}
.button_container{
    height: 30%;
    display: flex;
    justify-content: space-around;
    align-items: center;

}

.login_container, .register_container{
    height: fit-content;
}

.login_container input, .register_container input,.login_container button, .register_container button{
        border-radius: 0.5rem;
        padding: 0.5rem;
        padding-right: 3rem;
        font-size: 0.875rem;
        line-height: 1.25rem;
        box-shadow: 4px 4px var(--shadow);
        border: 2px solid var(--shadow);
        margin: 0.5rem;
}
.login_container button, .register_container button{
    margin: 1rem;
}

.register_container{
    display: none;
}

.login_field, .register_field{
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

